@media only screen and (max-width: 767px) {
  .main {

    .container {

      .title {
        font-size: 28px; } } } }


@media only screen and (max-width: 991px) {
  .main {
    margin: 0 auto; } }

@media only screen and (max-width: 575px) {
  .main {

    .container {

      .inputs {
        padding: 0;

        .inputContainer {

          input {
            min-width: 200px; }

          .submitBtn {
            min-width: 150px; } } } } } }
