@media only screen and (max-width: 1199px) {
  .titleScroll {

    .container {
      margin-left: 100px;
      margin-right: 100px; } } }

@media only screen and (max-width: 1099px) {
  .titleScroll {

    .container {
      margin-left: 50px;
      margin-right: 50px; } } }

@media only screen and (max-width: 991px) {
  .titleScroll {

    .container {

      .title {

        .subTitle {

          h1 {
            font-size: 3.5em; }

          .bord {} } }

      .content {
        flex-direction: column; } } } }

@media only screen and (max-width: 575px) {
  .titleScroll {
    background-image: url('../../public/images/backTitleScrollMobile4.png');

    .container {

      .title {
        padding: 0 10px;

        .subTitle {

          h1 {
            font-size: 3.5em; }

          .bord {} } } } } }

@media only screen and (max-width: 369px) {
  .titleScroll {

    .container {

      .title {

        .subTitle {
          display: table-caption; } } } } }
